









































































import Vue from 'vue';
import RomaUnit from '../../components/RomaUnit.vue';
import RomaCoverage from '../../components/RomaCoverage.vue';
import RomaPurpose from '../../components/RomaPurpose.vue';
import RomaOa from '../../components/RomaOa.vue';
import RomaActivityList from '../../components/RomaActivityList.vue';
import UnitPlan from "@/models/UnitPlan";

export default Vue.extend({
    name: 'OrganizerUnit',
    beforeRouteEnter (to, from, next) {
        //FIXME: Problemas con unidades con indice > 1
        let unit = UnitPlan.find(to.params.id);

        unit = UnitPlan.query().
                with('details').
                with('attitudes').
                with('preLearnings').
                with('skills').
                with('activities').
                whereId(unit.id).
                first();

        next(vm => vm.setUnit(unit));

    },
    created () {
        // Movilización entre tabs al inicio de la pagina
        this.acitvedTab = this.$route.hash.substr(1) ? this.$route.hash.substr(1) : 'tab-0';
    },
    data() {
        return {
            acitvedTab: null,
            tabs: [
                {
                    label: 'Propósito de la unidad',
                    icon: 'far fa-edit',
                    component: RomaPurpose,
                },
                {
                    label: 'Objetivos en la unidad',
                    icon: 'fas fa-bullseye',
                    component: RomaOa,
                },
                {
                    label: 'Cobertura de la unidad',
                    icon: 'fas fa-calendar-alt',
                    component: RomaCoverage,
                },
                {
                    label: 'Actividades en la unidad',
                    icon: 'fas fa-user-check',
                    component: RomaActivityList,
                },
            ],
            unit: null,
        };
    },
    computed: {
        dataReady () {
            return this.unit; // Fix render component with unit prop
        },
        breadcrumbs () {
          let breadcrumbs = [];

          breadcrumbs = this.$route.matched.map(
              route => {
                  if (route.meta && route.meta.text) {
                      const href = route.name === 'home' ? '/' : route.path;
                      const text = route.meta.idInText && (this.$route.params && this.$route.params.id) ? route.meta.text + ' ' + this.$route.params.id : route.meta.text;
                      return {text: text, disabled: !this.$route.hash && route.name === this.$route.name, href: href}
                  }
              }
          );

          if (this.$route.hash) {
              const index = this.acitvedTab.slice(this.acitvedTab.length-1, this.acitvedTab.length);
              breadcrumbs.push({text: 'Tab: ' + this.tabs[index].label, disabled: true, path: 'test'})
          }

          return breadcrumbs;
        }
    },
    watch: {
        $route: {
            deep: true,
            handler () {
                // para moverme entre los tabs si el hash es cambiado
                this.acitvedTab = this.$route.hash.slice(1, this.$route.hash.length);
            }
        }
    },
    methods: {
        setUnit (unit) {
            this.unit = unit;
        },
        changeHash (index) {
            // Cambio hash en cada cambio de tab
            this.$store.commit('highlightWords', ''); // Quito el resaltado de palabra
            window.location.hash = index;
        }
    },
    components: {
        RomaUnit,
        RomaOa,
        RomaCoverage,
    }
});
